/* Oba's Reset/Base */
html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  max-width: 1800px;
  margin: auto;
  position: relative;
}

img,
picture {
  max-width: 100%;
}

button,
input {
  font-family: inherit;
  appearance: none;
}

[hidden] {
  display: none !important;
}

/* Add all your variables here */
/* Add your mixins here */
body {
  font-family: "Manrope", system-ui, sans-serif;
}

h1,
h2,
h3 {
  font-family: "Inter", system-ui, sans-serif;
  color: #0d1168;
}

h1 {
  font-size: clamp(4rem, 10vw, 7.25rem);
  font-weight: 700;
  line-height: 1;
  letter-spacing: -4px;
  margin-bottom: 0.3em;
}

h2 {
  font-size: clamp(2.375rem, 4vw, 4rem);
  font-weight: 600;
  line-height: 1;
  margin-bottom: 0.3em;
}

h3 {
  font-size: clamp(1.875rem, 2.5vw, 2.25rem);
  font-weight: 400;
  line-height: 1.15;
  margin-bottom: 0.3em;
}

/* List */
p,
li {
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  font-weight: 400;
  line-height: 1.4;
  color: #1a1a1a;
}

/* Links */
a {
  font-size: clamp(1rem, 1.5vw, 1.125rem);
  color: #0d1168;
  transition: text-decoration-color 200ms ease-in-out;
}
a:hover {
  text-decoration-color: #fda312;
}

figcaption {
  text-align: center;
  font-size: 0.875rem;
  color: #1a1a1a;
}

.button, .button--close, .button--pry {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  width: fit-content;
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  padding: 0.8em 1em;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 300ms ease-in-out;
}
.button--pry {
  color: white;
  background-color: #fda312;
  border: 2px solid #0d1168;
  font-weight: 500;
}
.button--pry i {
  color: #0d1168;
}
.button--pry:hover {
  background-color: #ed9302;
}
.button--close {
  color: #fda312;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 50%;
  font-weight: 500;
}

.card {
  max-width: 380px;
}
.card-wrapper {
  display: flex;
  gap: 20px;
}
.card__img {
  background-color: blue;
  height: 120px;
}
.card__content {
  padding: 24px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  gap: 15px;
  justify-content: space-between;
}
.card__title {
  font-size: clamp(2.375rem, 4vw, 4rem);
  margin-bottom: 0;
}
.card__list {
  display: flex;
  gap: 1rem;
  padding: 0;
}
.card__item {
  padding: 2px 5px;
  border-radius: 4px;
  background-color: #e5e5e5;
}
.card__item--active {
  font-weight: bold;
  background-color: lightblue;
}
.card__link {
  background-color: darkblue;
  color: #fff;
  text-decoration: none;
  width: fit-content;
  width: -moz-fit-content;
  padding: 0.6em 1em;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 300ms ease;
}
.card__link:hover {
  background-color: blue;
  color: #fff;
}

/**** Navbar ****/
.navbar {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  gap: 3vw;
  padding: 12px 20px;
  background-color: #fff;
  border-bottom: 1px solid #fda312;
  z-index: 50;
}
.navbar .ham-icon {
  cursor: pointer;
}
.navbar .ham-icon span {
  display: block;
  width: 28px;
  height: 3px;
  background-color: #0d1168;
  border-radius: 20px;
}
.navbar .ham-icon span:nth-of-type(2) {
  margin: 3px 0;
}
.navbar__links {
  display: none;
}
.navbar__link {
  font-family: "Inter", system-ui, sans-serif;
  font-weight: 500;
}
.navbar .logo {
  max-width: 12em;
  margin-right: auto;
}

/* Mobile Menu */
.mobile-menu {
  position: fixed;
  top: 50px;
  left: 0;
  width: 200px;
  height: 100vh;
  padding: 50px 24px;
  background-color: #0d1168;
  border-right: 1px solid #fda312;
  z-index: 10;
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}
.mobile-menu.show-mobile-menu {
  transform: translateX(0);
}
.mobile-menu .button--close {
  margin: 0 0 80px 100px;
}
.mobile-menu__link {
  display: block;
  color: #fff;
  padding: 20px 0;
  letter-spacing: 1px;
  font-weight: 600;
  text-decoration: none;
}
.mobile-menu__link span {
  color: #fda312;
}
.mobile-menu__link:hover, .mobile-menu__link:active {
  color: #fda312;
}

/******************
    Media Queries 
*******************/
/* Tablet & Mobile */
@media (min-width: 768px) {
  .navbar .ham-icon {
    display: none;
  }
  .navbar .logo {
    margin-left: 20px;
  }
  .navbar__links {
    display: flex;
    align-items: center;
    gap: 3.5vw;
    margin-right: 30px;
  }
  .navbar__link {
    text-decoration: none #fda312;
  }
  .navbar .social-icons {
    display: flex;
    gap: 2vw;
  }
}
/* Mobile */
@media (max-width: 767px) {
  .social-link:nth-of-type(2),
.social-link:nth-of-type(3) {
    display: none;
  }
}
.footer {
  background-color: #0d1168;
  padding: 1.25em;
}
.footer__link {
  font-family: "Inter", system-ui, sans-serif;
  text-decoration: none;
  margin-right: 4vw;
  color: #fff;
}
.footer__link:hover {
  color: #fda312;
}

.form {
  max-width: 440px;
  padding: 4em 2em;
  background-color: inherit;
}
.form__heading {
  text-align: center;
}
.form__group {
  position: relative;
  min-height: 48px;
  margin-bottom: 1.5em;
}
.form__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: clamp(1.0625rem, 1.5vw, 1.25rem);
  border: 1px solid #fda312;
  outline: none;
  padding: 1rem;
  background: #fff;
}
.form__label {
  position: absolute;
  left: 1em;
  top: 0.9em;
  background: #fff;
  color: #888;
  font-size: 1rem;
  pointer-events: none;
  z-index: 1;
  transition: all 100ms ease-in;
}
.form__button {
  display: block;
  width: 100%;
  padding: 14px 0;
  outline: none;
  border: 2px solid #0d1168;
  background-color: #fda312;
  color: #fff;
  font-size: clamp(1rem, 1.5vw, 1.125rem);
  cursor: pointer;
  transition: 300ms;
}
.form__button:hover {
  box-shadow: 0 10px 36px #00000026;
}

/* Input:focus move up label */
.form__input:focus + .form__label {
  top: -0.5em;
  left: 0.8em;
  color: #0d1168;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid #fda312;
  border-radius: 2px;
  z-index: 1;
}

/*Input:focus sticky top label*/
.form__input:not(:placeholder-shown):not(:focus) + .form__label {
  top: -0.5em;
  left: 0.8em;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid #fda312;
  border-radius: 2px;
  z-index: 1;
}

/* textarea styling */
.form__textarea {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1em;
  font-size: clamp(1rem, 1.5vw, 1.125rem);
  border: 1px solid #fda312;
  outline: none;
  background: #fff;
}

/* textarea:focus move up label */
.form__textarea:focus + .form__label {
  top: -0.5em;
  left: 0.8em;
  color: #0d1168;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid #fda312;
  border-radius: 2px;
  z-index: 1;
}

/* textarea focus sticky top label */
.form__textarea:not(:placeholder-shown):not(:focus) + .form__label {
  top: -0.5em;
  left: 0.8em;
  font-size: 12px;
  font-weight: 500;
  padding: 2px;
  border-top: 1px solid #fda312;
  border-radius: 2px;
  z-index: 1;
}

.hero {
  background-image: url("../../images/hero-bg.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 20px;
  text-align: center;
}
.hero__profile {
  display: flex;
  align-items: center;
}
.hero__profile p {
  text-align: left;
  font-weight: bold;
  letter-spacing: 1.5px;
  color: #0d1267;
}
.hero__profile p span {
  color: #fda312;
}
.hero__role h1 {
  color: #181818;
}
.hero__role p {
  font-size: clamp(1.25rem, 2vw, 2rem);
}
.hero__points {
  margin: 1em 0;
  list-style: none;
  padding: 0;
}
.hero__points li {
  line-height: 1.6;
}
.hero__points i {
  color: #fda312;
  margin-right: 5px;
}
.hero__cta {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: 20px 0;
}
.hero__cta .link i {
  margin-right: 5px;
}
.hero__cta .link:hover i {
  color: #fda312;
}

/********************
    About & Blog Section 
*********************/
.about-blog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  padding: 60px 20px;
  border-top: 1px solid #f5f5ff;
}
.about-blog .about,
.about-blog .blog {
  margin: 20px auto;
  max-width: 600px;
}
.about-blog__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}
.about-blog__heading h2 {
  margin-bottom: 0;
}
.about-blog__heading i {
  color: #fda312;
}
.about-blog .about__points p {
  line-height: 1.8;
}
.about-blog .about__points i {
  color: #fda312;
  width: 20px;
  margin-right: 10px;
}
.about-blog .about__stack {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
.about-blog .divider {
  border-style: solid;
  border-color: #0d1168;
}
.about-blog .blog__content {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
}
.about-blog .blog__content > *:nth-child(1) {
  flex: 1 1 40%;
  min-width: 200px;
}
.about-blog .blog__content > *:nth-child(2) {
  flex: 1 1 55%;
}
.about-blog .blog__image {
  height: 200px;
  background: center/cover url("../../images/me-suit-smiling.png");
  object-fit: cover;
  object-position: center;
}
.about-blog .blog__title {
  font-size: clamp(1.875rem, 2.5vw, 2.25rem);
  font-family: "Inter", system-ui, sans-serif;
  margin-bottom: 0.8em;
}

/********************
   Offer & Testimonial Section
*********************/
.offer-testimonial {
  background-image: url("../../images/bg-transparent2.png");
  padding: 60px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  gap: 30px;
  border-top: 1px solid #fda312;
  border-bottom: 1px solid #fda312;
}
.offer-testimonial .offer {
  max-width: 700px;
}
.offer-testimonial .offer h2,
.offer-testimonial .offer h3 {
  margin-bottom: 10px;
}
.offer-testimonial .offer__points li {
  list-style: none;
  margin-top: 8px;
}
.offer-testimonial .offer__points li span {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #fda312;
  text-align: center;
  line-height: 1;
  font-size: 1rem;
  font-weight: 600;
  margin-right: 15px;
}
.offer-testimonial .testimonial__profile {
  max-width: 300px;
  text-align: center;
  padding: 40px 20px;
  margin: auto;
  background-color: #fff;
  border: 1px dashed #0d1168;
  border-radius: 1em;
  box-shadow: 0 5px 10px #00000025;
}
.offer-testimonial .testimonial__profile__img {
  border-radius: 50%;
  margin-bottom: 1em;
}
.offer-testimonial .testimonial__text, .offer-testimonial .testimonial__name {
  font-size: clamp(1rem, 1.5vw, 1.125rem);
}
.offer-testimonial .testimonial__name {
  font-weight: 700;
  margin-top: 8px;
}
.offer-testimonial .testimonial__position {
  font-size: 0.875rem;
  letter-spacing: 1px;
}

/*****************
    Portfolio Section 
******************/
.portfolio {
  padding: 40px 20px;
}
.portfolio__heading, .portfolio__description {
  text-align: center;
}
.portfolio__description {
  margin-bottom: 1em;
}
.portfolio figure {
  margin: auto auto 60px;
  max-width: 1100px;
}
.portfolio figure p:nth-child(2) {
  margin-bottom: 0.5em;
}
.portfolio figure i {
  font-size: 12px;
  text-decoration: none;
}
.portfolio figure img {
  margin: auto auto 10px;
}
.portfolio p a {
  color: #fda312;
  font-weight: bold;
}

/*****************
    Services Section 
******************/
.services {
  background-image: url("../../images/bg-transparent2.png");
  padding: 60px 20px;
  border-top: 1px solid #fda312;
  border-bottom: 1px solid #fda312;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.services__heading, .services__description {
  text-align: center;
}
.services__description {
  margin-bottom: 1em;
}
.services__slides {
  max-width: 1100px;
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr;
}
.services__slides .slide {
  padding: 80px 30px 30px;
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 0px 5px 12px #00000025;
}
.services__slides .slide img {
  margin-bottom: 15px;
}

/********************
    Picture Section 
*********************/
.gallery {
  max-width: 1100px;
  margin: auto;
  padding: 80px 0;
}
.gallery .my-image {
  height: 320px;
}
.gallery .my-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

/*****************
    Contact Section
******************/
.contact {
  background-color: #f5f5ff;
  border-top: 1px solid #fda312;
  padding: 60px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact__channel {
  background-color: #fff;
  border: 2px solid #fda312;
  padding: 1em 1.5em;
  border-radius: 1rem;
  width: 100%;
  max-width: 400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1em;
  text-decoration: none;
  transition: background-color 200ms ease-in-out, border-color 200ms ease-in-out, transform 200ms ease-out;
}
.contact__channel:hover {
  background-color: #f5f5ff;
  border-color: #0d1168;
  transform: scale(1.02);
}
.contact__channel:hover .contact__icon {
  background-color: #fff;
}
.contact__channel:hover i {
  color: #fda312;
}
.contact__text {
  font-size: clamp(1.25rem, 2vw, 2rem);
  font-weight: 600;
  color: #000;
}
.contact__icon {
  background-color: #f5f5ff;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 50%;
  transition: background-color 200ms ease-out;
}
.contact__icon i {
  margin-top: 15px;
  font-size: 1.25rem;
}

/*****************
    Media Queries 
******************/
/* Tablet & Computer */
@media (min-width: 768px) {
  .hero {
    justify-content: center;
    height: 60vh;
  }
  .hero .hero__points {
    display: flex;
    gap: 20px;
  }
  .hero__cta {
    flex-direction: row;
  }

  .about-blog {
    flex-direction: row;
    gap: 3em;
    padding: 100px 1em;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
  }
  .about-blog .about,
.about-blog .blog {
    flex: 1 1 48%;
  }

  .gallery {
    padding: 120px 0;
  }

  .offer-testimonial {
    flex-direction: row;
    justify-content: space-around;
  }

  .portfolio {
    padding: 120px 20px;
  }

  .services {
    padding: 120px 20px;
  }
  .services__slides {
    grid-template-columns: repeat(3, 1fr);
  }

  .faq {
    flex-direction: row;
    align-items: center;
    padding: 120px 20px;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
  .faq .left-side,
.faq .right-side {
    flex: 1 1 45%;
  }
}
/* Large Desktop */
@media (min-width: 1300px) {
  .hero {
    height: 81vh;
  }

  .portfolio__more-text p:nth-of-type(2) {
    margin-left: 100px;
  }
}